<template>
	<div>
		<div class="bg"></div>
		<div class="container">
				<div style="height:100px"></div>
				<h1 class="title">一路云停企业管理平台</h1>
					<transition name="bounce">
						<div class="login-box" v-show="show">
							<el-form>
									<div class="form-container">
										<el-form-item>
											<div class="loginTips">{{errorTips}}</div>
										</el-form-item>
										<el-form-item>
											<el-input placeholder="登陆账号"  suffix-icon="fa fa-user" v-model="loginForm.userName" @change="changeVal"></el-input>
										</el-form-item>
										<el-form-item>
											<el-input type="password" placeholder="登陆密码" suffix-icon="fa fa-key" v-model="loginForm.passWord" @change="changeVal"></el-input>
										</el-form-item>
										<el-form-item>
											<el-input  placeholder="验证码" v-model="loginForm.passCode">
												<template slot="append"><img :src="codeImg" alt="验证码" @click="changePassCode" @change="changeVal"></template>
											</el-input>
										</el-form-item>
										<!-- <el-form-item>
													<el-checkbox v-model="checked">记住密码</el-checkbox>
										</el-form-item> -->
										<el-form-item>
											<el-button type="primary" class="login-btn" @click="login">登陆</el-button>
										</el-form-item>
									</div>
							</el-form>
						</div>	
					</transition>
					<!-- <div  class="no-content"></div> -->
					<el-row style="margin-top:100px;">
						 <el-col :span="24" align="center">
							 	<div class="foot-desc"><span>厦门轨道交通集团  |  厦门路桥信息股份有公司</span></div>
						 </el-col>
					</el-row>
				
    </div>
	
	</div>
</template>
<script>
import {Ajax} from '@/js/ajax.js'
export default {
	data(){
		return {
			checked:false,
			show:false,
			checkedResult:false,
			errorTips:"",
			codeImg:httpConfig+'code?d=' + Date.parse(new Date()),
			loginForm:{
				userName:null,
				passWord:null,
				passCode:null
			}
		}
	},
	created(){

	},
	mounted(){
		this.show=true;
	},
	methods:{
		//切换验证码
		changePassCode(){
			this.codeImg=httpConfig+'code?d=' + Date.parse(new Date());
		},
		changeVal(val){
			if(val){
				this.errorTips='';
			}

		},
		regValue(){
			let userName=this.loginForm.userName;
			let passWord=this.loginForm.passWord;
			let passCode=this.loginForm.passCode;
			this.checkedResult=false;
			if(!userName){
				this.errorTips="你还没有输入账号！"
			}else if(!passWord){
				this.errorTips="你还没有输入密码！"
			}else if(!passCode){
				this.errorTips="你还没有输入验证码！"
			}else{
				this.errorTips="";
				this.checkedResult=true;
			}
		},
		//保存登陆cookie
	// 	saveCookie(){
	// 		let userName=this.loginForm.userName;
	// 		let passWord=this.loginForm.passWord;
	// 		let checked=this.checked;
	// 		this.setCookie('userName',userName,7);
	// 		this.setCookie('passWord',passWord,7);
	// 		this.setCookie('checked',checked,7);
	// 	},
	// 	//设置cookie
	// 	setCookie:function(name,value,day){
	//     var date = new Date();
	//     date.setDate(date.getDate() + day);
	//     document.cookie = name + '=' + value + ';expires='+ date;
	//  	},
	// 	//获取cookie
	// 	getCookie:function(name){
	// 		var reg = RegExp(name+'=([^;]+)');
	// 		var arr = document.cookie.match(reg);
	// 		if(arr){
	// 		return arr[1];
	// 		}else{
	// 		return '';
	// 		}
	// 	},
	// 	//删除cookie
	// 	delCookie:function(name){
	// 		this.setCookie(name,null,-1);
	// 	},
		//登陆
		login(){
			let that=this;
			this.regValue();
			let params={};
			params.passWord=this.$md5(this.loginForm.passWord);
			params.userName=this.loginForm.userName;
			params.passCode=this.loginForm.passCode;
			if(this.checkedResult){
				Ajax({
					method:'post',
					url : httpConfig+'login',
					data : params,
					done : function(error,data,resdata) {
						if(error){
							that.codeImg=httpConfig+'code?d=' + Date.parse(new Date());
							that.$message({
								showClose: true,
								message: error.msg,
								type: 'error'
							});
						}else{
							// if(that.checked){
							// 	that.saveCookie();
							// }else{
							// 	that.delCookie('userName');
							// 	that.delCookie('passWord');
							// 	that.delCookie('checked');
							// }
					
							that.$router.push('/');
						}	
					},function(error) {
							that.$message({
								showClose: true,
								message: error.msg,
								type: 'error'
							});
					},					
				})
			}	
		}
	}
}
</script>
<style lang="scss" src="@/styles/login.scss" scoped>
	
</style>
<style >
		.form-container .el-input__suffix-inner{
			font-size: 16px; /*no*/
	
		}
	 .form-container .el-input__inner{
		height: 40px ; /*no*/
		line-height: 40px ; /*no*/
		font-size: 16px; /*no*/
	}
	.form-container .el-input__icon{
    width: 24px; /*no*/
	}
	.form-container .el-checkbox__inner{
		width: 12px; /*no*/
		height: 12px; /*no*/
	}
	.form-container .el-checkbox{
		font-size: 16px;/*no*/
  }
</style>




